.cards {
  margin: 0 auto 66px;
  max-width: 882px;
  display: grid;
  grid-template-columns: repeat(auto-fit, 282px);
  grid-column-gap: 18px;
  grid-row-gap: 20px;
  justify-content: center;
}

@media screen and (max-width: 899px) {
  .cards {
    margin-left: 19px;
    margin-right: 19px;
  }
}